body {
  margin: 0;
  padding: 0;
  font-family: sans-serif;
}

#grid {
  display: grid;
  grid-template-columns: 50px 50px 50px 50px 50px 50px 50px 50px 50px 50px;
  text-align: center;
  padding-left: 25%
}
span {
  height: 100%;
  width: 100%;
  border-radius: 0px;
  background-color: black;
}


